class Auth {
  authenticated = false;
  sub = null;
  email = null;
  name = null;

  login(cb, user) {
    this.authenticated = true;
    cb();
  }

  logout(cb) {
    this.authenticated = false;
    cb();
  }

  isAuthenticated() {
    return this.authenticated;
  }

  getSub() {
    return this.sub;
  }

  getEmail() {
    return this.email;
  }

  getName() {
    return this.name;
  }

  getToken(){
    return this.token
  }
}

export default new Auth();
