import React, { useEffect, useState } from 'react';
import { useHistory, Link } from 'react-router-dom';
import { FaUserAlt, FaLock, FaRegEnvelope } from 'react-icons/fa';
import { withStyles } from '@material-ui/core/styles';
import { Paper, Box, Divider, TextField, InputAdornment } from '@material-ui/core';
import AuthButton from '../AuthButton/AuthButton';
import { userRegFunc, userLoginFunc } from '../../actions/user';
import PropTypes from 'prop-types';
import stylesForm from './stylesForm';
import auth from '../../auth';

const Form = ({ classes, type }) => {
  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [formType, setformType] = useState(null);
  const isLoginForm = formType === 'login' ? true : false;

  let history = useHistory();

  useEffect(() => {
    if (type === 'login') setformType('login');
  }, []);

  const changeFormType = () => {
    formType === 'login' ? setformType('signup') : setformType('login');
  };

  const handleSignIn = () => {
    const loginUserInfo = {
      email: email,
      password: password,
    };
    userLoginFunc(loginUserInfo).then((res) => {
      auth.login(() => {
        if (res.status === 201) {
          auth.sub = res.data._id;
          auth.email = res.data.email;
          auth.name = res.data.fullName;
          auth.token = res.data.access_token;
          history.push('/dashboard');
        }
      });
    });
  };

  const handleSignUp = () => {
    const userInfos = {
      name: name,
      email: email,
      password: password,
    };
    userRegFunc(userInfos).then((res) => {
      auth.login(() => {
        if (res.status === 201) {
          auth.sub = res.data._id;
          auth.email = res.data.email;
          auth.name = res.data.fullName;
          auth.token = res.data.access_token;
          history.push('/profile_settings');
        }
      });
    });
  };

  const loginText = {
    header: 'Log into your account',
    helpText: "Don't have an account? ",
    redirectText: 'Sign Up',
    redirectPath: '/signup',
    handleClick: handleSignIn,
  };

  const signupText = {
    header: 'Register an account',
    helpText: 'Already have an account? ',
    redirectText: 'Login',
    redirectPath: '/login',
    handleClick: handleSignUp,
  };

  return (
    <Paper elevation={6} className={classes.paper}>
      <h2 className={classes.loginHeader}>{isLoginForm ? loginText.header : signupText.header}</h2>
      {formType === 'login' ? (
        <>
          <TextField
            name="email"
            label="Email"
            type="text"
            variant="outlined"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaRegEnvelope />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="password"
            label="Password"
            variant="outlined"
            margin="normal"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
            }}
          />
        </>
      ) : (
        <>
          <TextField
            name="fullname"
            label="Name"
            type="text"
            variant="outlined"
            value={name}
            size="small"
            margin="normal"
            onChange={(e) => setName(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaUserAlt />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="email"
            label="Email"
            type="text"
            variant="outlined"
            value={email}
            size="small"
            margin="normal"
            onChange={(e) => setEmail(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaRegEnvelope />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="password"
            label="Password"
            type="password"
            variant="outlined"
            value={password}
            size="small"
            margin="normal"
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="password"
            label="Confirm Password"
            type="password"
            variant="outlined"
            value={password}
            size="small"
            margin="normal"
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
            }}
          />
        </>
      )}

      <AuthButton type={type} click={isLoginForm ? loginText.handleClick : signupText.handleClick} />
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          {isLoginForm ? loginText.helpText : signupText.helpText}
          <Link to={formType === 'login' ? loginText.redirectPath : signupText.redirectPath} onClick={changeFormType}>
            {isLoginForm ? loginText.redirectText : signupText.redirectText}
          </Link>
        </Box>
      </Box>
    </Paper>
  );
};

Form.propTypes = {
  classes: PropTypes.object.isRequired,
  type: PropTypes.string.isRequired,
};

export default withStyles(stylesForm)(Form);
