import React from 'react';
import './AuthButton.css';

const AuthButton = (props) => {
  const { type, click } = props;

  return (
    <div id="authButton" className="buttonContainer" onClick={click}>
      <span>{type === 'login' ? 'Sign in with your email' : 'Sign up with your email'}</span>
    </div>
  );
};

export default AuthButton;
