import axios from 'axios';
import { signup_url, signin_url, unique_url, profile_update, getuser_url } from './constant';

export const userLoginFunc = (userInfos) => {
  return axios
    .post(signin_url, userInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const userRegFunc = (userInfos) => {
  return axios
    .post(signup_url, userInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const uniqueUrlFunc = (url) => {
  return axios
    .get(`${unique_url}/?url=${url}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const updateMeetingProfileFunc = (id, profileInfos) => {
  return axios
    .put(`${profile_update}/${id}`, profileInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const getUserByIdFunc = (id) => {
  return axios
    .get(`${getuser_url}/${id}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
